.App {
    font-family: Arial, sans-serif;
    margin: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 550px;
    gap: 15px;
  }
  
  #message-box {
    border: 1px solid #000;
    padding: 15px;
  }
  
  #error {
    color: red;
    font-weight: bold;
    margin-top: 10px;
  }
  
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ff0000bd;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .overlay-content {
    background-color: #ff0000;
    padding: 20px;
    text-align: center;
    border-radius: 8px;
    max-width: 100%;
    box-sizing: border-box;
  }
  
  .overlay-content button {
    background: black;
    color: white;
    border-radius: 50px;
    width: 150px;
    height: 40px;
  }
  
  @media screen and (max-width: 480px) {
    .overlay-content {
      padding: 10px;
      max-width: 90%;
    }
  }
  
  @media screen and (min-width: 768px) {
    .container {
      width: 768px;
    }
  }
  
  @media screen and (min-width: 1440px) {
    .container {
      width: 1440px;
    }
  }
  
  .overlay div {
    background-color: #ff0000;
    padding: 20px;
    text-align: center;
    border-radius: 8px;
  
    button {
      background: black;
      color: white;
      border-radius: 50px;
      width: 150px;
      height: 40px;
    }
  }
  
  
  .show {
    visibility: visible;
    background: #ff0000cc;
    opacity: 0.8;
  }
  
  
  
  
  .container {
    width: 320px;
    margin: 0 auto;
    padding: 0 10px 100px 10px;
  }
  
  @media screen and (min-width: 768px) {
    .container {
      width: 768px;
    }
  }
  @media screen and (min-width: 1440px) {
    .container {
      width: 1440px;
    }
  }
  
  .subtitle {
    font-size: 26px;
    text-transform: uppercase;
    font-weight: 900;
    color: var(--main-text-color);
    margin-bottom: 30px;
  }